import {getRechargeRecord, getUsedRecord} from "../../api/orders";
import Status from "../component/status";

export default {
    name: "rechargeRecord",
    components: {Status},
    data(){
        return {
            list:[]
        }
    },
    async onLoad(options){
        const {cardno } = options;
        uni.showLoading({title:'加载中...'});
        let  list = await  getUsedRecord({cardno});
        uni.hideLoading();
        this.list = list
        console.log(`调试:使用记录查询结果`, list)
    }
}
